import { generateTypes } from './generate.types';

const INITIAL_STATE = {
  generate: {
      defValues: {
          login : 847392002,
          password : 'Hs@351!saS!@',
          nickname : 'Valeron',
          telegram : 'Valeron.Valeron',
          comment : 'Valera Old Farmer',
      },
      store: [{
          login : 847392002,
          password : 'Hs@351!saS!@',
          nickname : 'Valeron',
          telegram : 'Valeron.Valeron',
          comment : 'Valera Old Farmer',
          ticketID: 2133213213,
      }]
  }
};
let storeClone,  currentItemObj;
const generateReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
      case generateTypes.SET_NEW_VALUE_FOR_STORE_ITEM:

          storeClone = [...state.generate.store];

          let currentItemObj = storeClone.find((storeObject) => {
              return storeObject.ticketID === action.payload.id;
          });


          if (currentItemObj) {
              currentItemObj[action.payload.item] = action.payload.itemValue;
          }



          return {
              ...state,
              generate: {
                  ...state.generate,
                  store : storeClone
              }
          };
      case generateTypes.CREATE_ITEM:

          storeClone = [...state.generate.store];

          storeClone.push(action.payload)


          return {
              ...state,
              generate: {
                  ...state.generate,
                  store : storeClone
              }
          };

      case generateTypes.DELETE_ITEM:

          storeClone = [...state.generate.store];

          let index = storeClone.findIndex(obj => obj.ticketID === action.payload);

          storeClone.splice(index, 1);

          return {
              ...state,
              generate: {
                  ...state.generate,
                  store : storeClone
              }
          };

    default:
      return state;
  }
};

export default generateReducer;
