import React from 'react';
import './style.scss';

const Preloader = (props) => {

    let preloaderClasses = "preloader__wrapper";

    if(props.fullscreen){
        preloaderClasses = "preloader__wrapper preloader__wrapper--fullscreen";
    }

    return (
        <div className={preloaderClasses}>
            <div className="preloader--circle">
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
            </div>
        </div>
    );
};

export default Preloader;