import React, {Suspense} from 'react';
import { ThemeProvider } from '@material-ui/styles';
import {BrowserRouter} from "react-router-dom";

import './styles/App.scss';
import theme from './components/UI/theme';


import {setLicenseDaysSettingsModal} from "./redux/app/app.actions";
import {connect} from "react-redux";
import Preloader from "./components/Preloader/Preloader";
import { I18nProvider, LOCALES } from './i18n';

const Navigation = React.lazy(() => import('./components/Navigation/Navigation'));
const DashBoard = React.lazy(() => import('./components/Dashboard/DashBoard'));
const ModalWindow = React.lazy(() => import('./components/ModalWindows/ModalWindow'));
const AuthWindow = React.lazy(() => import('./components/AuthWindows/AuthWindow'));



const App = (props) => {

    let dashboardComponent = (<><Navigation/><DashBoard /><ModalWindow /><AuthWindow /></> );
    let authComponent = (<AuthWindow />);

    return (
        <BrowserRouter>
            <ThemeProvider theme={theme}>
                <Suspense fallback={<Preloader fullscreen={true}/>}>
                    <I18nProvider locale={props.appState.appLanguage.currentAppLanguage}>
                {props.appState.dashboard ? dashboardComponent : authComponent}
                    </I18nProvider>
                </Suspense>
            </ThemeProvider>
        </BrowserRouter>
    );
}

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (bool, id) => dispatch(setLicenseDaysSettingsModal(bool, id)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App);