import { inviteTypes } from './invite.types';

const INITIAL_STATE = {
  invite: {
      defValues: {
          login : 'CU847392002',
          newLogin : 847392002,
          password : 'Hs@351!saS!@',
          nickname : 'Valeron',
          telegram : 'Valeron.Valeron',
          comment : 'Valera Old Farmer',
      },
      store: [{
          login : 'CU847392002',
          newLogin : 847392002,
          password : 'Hs@351!saS!@',
          nickname : 'Valeron',
          telegram : 'Valeron.Valeron',
          comment : 'Valera Old Farmer',
          ticketID: 2133213213,
      }]
  }
};
let storeClone,  currentItemObj;
const inviteReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
      case inviteTypes.SET_NEW_VALUE_FOR_INVITE_STORE_ITEM:

          storeClone = [...state.invite.store];

          console.dir(currentItemObj[action.payload.item] );
          console.dir(action.payload.itemValue);


          let currentItemObj = storeClone.find((storeObject) => {
              return storeObject.ticketID === action.payload.id;
          });



          if (currentItemObj) {
              currentItemObj[action.payload.item] = action.payload.itemValue;
          }

          return {
              ...state,
              invite: {
                  ...state.invite,
                  store : storeClone
              }
          };
      case inviteTypes.CREATE_INVITE_ITEM:

          storeClone = [...state.invite.store];

          storeClone.push(action.payload)


          return {
              ...state,
              invite: {
                  ...state.invite,
                  store : storeClone
              }
          };

      case inviteTypes.DELETE_INVITE_ITEM:

          storeClone = [...state.invite.store];

          let index = storeClone.findIndex(obj => obj.ticketID === action.payload);

          storeClone.splice(index, 1);

          return {
              ...state,
              invite: {
                  ...state.invite,
                  store : storeClone
              }
          };

    default:
      return state;
  }
};

export default inviteReducer;
